nav {
    background-color: #f0f0f0; /* Light gray background */
    padding: 1rem;
    display: flex;
    justify-content: center; /* Centers the content horizontally */
  }
  
  nav ul {
    list-style: none;
    display: flex;
    gap: 2.5rem; /* Space between menu items */
    margin: 0;
    padding: 0;
  }
  
  nav ul li a {
    text-decoration: none;
    color: blue;
    font-weight: bold;
  }
  
  nav ul li a:hover {
    color: darkblue; /* Change color on hover for better interactivity */
  }
  

  /* Navbar.css */

nav {
    background-color:pink;
    padding: 2rem;
    display: flex;
    justify-content: center;
  }
  
  nav ul {
    list-style: none;
    margin: 0 1rem; /* Adds horizontal space between list items */
  }
  
  nav ul a {
    text-decoration: none;
    color: black;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 1.2rem;
    transition: color 0.3s ease;
  }
  
  nav ul a:hover {
    color: #ff6347; /* Tomato color for hover effect */
  }
  
  nav ul a:active {
    color: #ff4500; /* OrangeRed color for active state */
  }
  